import React from "react";

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = { name: '',
                  subject: '',
                  message: '',
                  isLoading: false,
                  isSent: false     
                    };
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(event) { 
    const target = event.target;
    const value = target.value.replace(/\n/g, '%0a');
    const name = target.name;
    this.setState({ 
      [name]: value 
    }); 
  }
  componentDidMount(){
    document.getElementById("main").scrollIntoView();
  }
  render() {
    const url = 'https://api.whatsapp.com/send?phone=6283115937891&text=My name is '+this.state.name+'%0a'+this.state.subject+'%0a'+this.state.message;
    return (
      <section id="contact" className="contact">
        <div className="container">
          <div className="section-title">
            <h2>Contact</h2>
          </div>
          <div className="row mt-1">
            <div className="col-lg-4">
              <div className="info">
                <div className="address">
                  <i className="icofont-google-map" />
                  <h4>Location:</h4>
                  <p>Jln. Seririt - Gilimanuk, Buleleng, Bali - Indonesia</p>
                </div>
                <div className="email">
                  <i className="icofont-envelope" />
                  <h4>Email:</h4>
                  <p>kadekdodikwirawan@gmail.com</p>
                </div>
                <div className="phone">
                  <i className="icofont-phone" />
                  <h4>Call:</h4>
                  <p>+6283115937891</p>
                </div>
              </div>
            </div>
            <div className="col-lg-8 mt-5 mt-lg-0">
              <form
                autoComplete="on"
                className="php-email-form"
              >
                <div className="form-row">
                  <div className="col-md-6 form-group">
                    <input
                      type="text"
                      name="name"
                      className="form-control"
                      id="name"
                      placeholder="Your Name"
                      value={this.state.value} onChange={this.handleChange}
                      required
                    />
                    <div className="validate" />
                  </div>
                  <div className="col-md-6 form-group">
                    <input
                      type="text"
                      className="form-control"
                      name="subject"
                      id="subject"
                      placeholder="Subject"
                      value={this.state.value} onChange={this.handleChange}
                      required
                    />
                    <div className="validate" />
                  </div>
                </div>

                <div className="form-group">
                  <textarea
                    className="form-control"
                    name="message"
                    id="message"
                    rows={5}
                    placeholder="Message"
                    value={this.state.value} onChange={this.handleChange}
                    required
                  />
                  <div className="validate" />
                </div>
                <div className="mb-3">
                 {this.state.isLoading === true ? <div className="loading">Loading</div> : ''}
                  {/* <div className="error-message" /> */}
                  {this.state.isSent === true ? 
                  <div className="sent-message">
                    Your message has been sent. Thank you!
                  </div> : ''}
                </div>
                <div className="text-center">
                  <a href={url} className="tombol">Send Message</a>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Contact;
